:root {
  --color-primary: #6741d9;
  --color-primary-light: #7950f2;
  --color-text: #dee2e6;
  --color-text-dark: #adb5bd;
  --color-background-100: #343a40;
  --color-background-500: #2b3035;
  --color-background-900: #212529;
  --color-red: #fa5252;
  --color-red-dark: #e03131;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
/* width */
::-webkit-scrollbar {
  width: 10px;
  border-radius: 1rem;
}

/* Track */
::-webkit-scrollbar-track {
  background: var(--color-background-500);
  border-radius: 1rem;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #7f7f7f;
  border-radius: 1rem;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: var(--color-text);
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;

  color: var(--color-text);
  background-color: var(--color-background-900);
}

/* ******* */

.nav-bar {
  display: flex;
  align-items: center;
  gap: 1rem;
  justify-content: space-between;
  padding: 0.8rem 1rem;
  background-color: var(--color-primary);
  transition: all 0.3s;
  font-size: 1.3rem;
}
.main {
  padding: 2rem 1rem;
  display: flex;
  gap: 2.4rem;
  justify-content: center;
}

.box {
  width: 50%;
  background-color: var(--color-background-500);
  border-radius: 0.9rem;
  position: relative;
  overflow: hidden;
  padding: 0.5rem;
  min-height: calc(100vh - 10rem);
  max-height: calc(100vh - 10rem);
}

.main .box:nth-child(2) .details{
  overflow-y: scroll ;
  height: 100%;
}



.loader {
  text-align: center;
  text-transform: uppercase;
  font-size: 2rem;
  padding: 2rem;
}

.error {
  text-align: center;
  font-size: 2rem;
  padding: 2rem;
}

/* ******* */

.logo {
  display: flex;
  align-items: center;
  gap: 0.3rem;
}

.logo h1 {
  font-weight: 600;
  color: #fff;
}
input,
button {
  all: unset;
}

.search {
  padding: 1rem 1rem;
  border-radius: 0.5rem;
  color: var(--color-text);
  background-color: var(--color-primary-light);
  transition: all 0.3s;
}

.search::placeholder {
  color: var(--color-text-dark);
}

.search:focus {
  box-shadow: 0 2.4rem 2.4rem rgba(0, 0, 0, 0.1);
  transform: translateY(-2px);
}

.btn-toggle {
  position: absolute;
  top: 1rem;
  right: 1rem;
  width: 40px;
  height: 40px;
  text-align: center;
  border-radius: 50%;
  background-color: var(--color-background-900);
  color: var(--color-text);
  font-weight: bold;
  cursor: pointer;
  z-index: 100;
}

.list {
  list-style: none;
  overflow-y: scroll;
  height: 100%;
}

.list li {
  position: relative;
  display: grid;
  grid-template-columns: 4rem 1fr;
  grid-template-rows: auto auto;
  column-gap: 2.4rem;
  font-size: 1.6rem;
  align-items: center;
  padding: 1.6rem 1rem;
  border-bottom: 1px solid var(--color-background-100);
}

.list.list-movies li {
  cursor: pointer;
  transition: all 0.3s;
}

.list.list-movies li:hover {
  background-color: var(--color-background-100);
}
img{
  border-radius: 0.5rem;
}
.list img {
  width: 100%;
  grid-row: 1 / -1;
  border-radius: 0.8rem;
  max-height: 25rem;
  object-fit: contain;
}

.list h3 {
  font-size: 1.8rem;
}

.list div {
  display: flex;
  align-items: center;
  gap: 2.4rem;
}

.list p {
  display: flex;
  align-items: center;
  gap: 0.8rem;
}

.btn-delete {
  position: absolute;
  right: 2.4rem;
  text-align: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: none;
  background-color: var(--color-red);
  color: var(--color-background-900);
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s;
}

.btn-delete:hover {
  background-color: var(--color-red-dark);
}

/* ******* */

.summary {
  padding: 1rem 2rem;
  border-radius: 0.9rem;
  background-color: var(--color-background-100);
  box-shadow: 0 1.2rem 2.4rem rgba(0, 0, 0, 0.2);
}
.summary > div{
  flex-wrap: wrap;
}
.summary h2 {
  text-transform: uppercase;
  font-size: 1.6rem;
  margin-bottom: 0.6rem;
}

.summary div {
  display: flex;
  align-items: center;
  gap: 2.4rem;
  font-size: 1.6rem;
  font-weight: 600;
}

.summary p {
  display: flex;
  align-items: center;
  gap: 0.8rem;
}

/* ******* */

.details {
  line-height: 1.4;
  font-size: 1.4rem;
}

.details header {
  display: flex;
}

.details section {
  padding: 4rem;
  display: flex;
  flex-direction: column;
  gap: 1.6rem;
}

.details img {
  width: 33%;
}

.details-overview {
  width: 100%;
  padding: 2.4rem 3rem;
  background-color: var(--color-background-100);
  display: flex;
  flex-direction: column;
  gap: 1.4rem;
}

.details-overview h2 {
  font-size: 2.4rem;
  margin-bottom: 0.4rem;
  line-height: 1.1;
}

.details-overview p {
  display: flex;
  align-items: center;
  gap: 0.8rem;
}

.rating {
  background-color: var(--color-background-100);
  border-radius: 0.9rem;
  padding: 2rem 2.4rem;
  margin-bottom: 0.8rem;
  font-weight: 600;
  display: flex;
  flex-direction: column;
  gap: 2.4rem;
  margin: auto;
  width: 80%;
  justify-content: center;
  align-items: center;
}

.btn-add {
  background-color: var(--color-primary);
  color: var(--color-text);
  border: none;
  border-radius: 10rem;
  font-size: 1.4rem;
  padding: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s;
  text-align: center;
  width: fit-content;
}

.btn-add:hover {
  background-color: var(--color-primary-light);
}

.seconds {
  background-color: var(--color-background-100);
  width: 8rem;
  border-radius: 10rem;
  font-weight: 600;
  text-align: center;
  padding: 0.4rem;
  margin-top: 2.4rem;
}

.btn-back {
  position: absolute;
  top: 0.6rem;
  left: 0.6rem;
  height: 3.2rem;
  aspect-ratio: 1;
  border-radius: 50%;
  border: none;
  /* background-color: var(--color-text); */
  background-color: #fff;
  color: var(--color-background-500);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.8);
  font-family: sans-serif;
  font-size: 2.4rem;
  font-weight: bold;
  cursor: pointer;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
}

/*
SPACING SYSTEM (px)
2 / 4 / 8 / 12 / 16 / 24 / 32 / 40 / 48 / 64 / 80 / 96 / 128

FONT SIZE SYSTEM (px)
10 / 12 / 14 / 16 / 18 / 20 / 24 / 30 / 36 / 44 /52 / 62 / 74 / 86 / 98
*/

@media (max-width: 570px) {
  .main .box:nth-child(1) .list img {
    grid-row: span 1;
    grid-column: span 2;
  }
  .main .box:nth-child(1) .list img  {
    grid-template-columns: auto auto;
    row-gap: 1rem;
  }
  .box .details section{
      padding:2rem 0.5rem;
  }
  .box header{
    flex-direction: column;
    justify-content: center;
    align-items:center ;
    gap: 1rem;
  }
 .box header img{
    width: 80%;
  }
}

@media (max-width: 960px) {
  .main {
    flex-direction: column;
  }
  .box {
    width: 100%;
    height:90vh;
  }
  .nav-bar {
    flex-direction: column;
  }
}
